import {BorderBox, Flex, Link, TextInput, themeGet} from '@primer/components'
import {LinkExternalIcon} from '@primer/styled-octicons'
import {Link as GatsbyLink, navigate} from 'gatsby'
import preval from 'preval.macro'
import React, { useState, useEffect, useRef } from 'react'
import styled from 'styled-components'

// This code needs to run at build-time so it can access the file system.
const repositoryUrl = preval`
  const readPkgUp = require('read-pkg-up')
  const getPkgRepo = require('get-pkg-repo')
  try {
    const repo = getPkgRepo(readPkgUp.sync().package)
    module.exports = \`https://github.com/\${repo.user}/\${repo.project}\`
  } catch (error) {
    module.exports = ''
  }
`

const NavLink = styled(Link)`
  &.active {
    font-weight: ${themeGet('fontWeights.bold')};
    color: ${themeGet('colors.gray.8')};
  }
`

function EditableInput(props) {

  // We use hooks to declare "initial" states
  const inputRef = useRef(null);
  const [inputVisible, setInputVisible] = useState(false);
  const [text, setText] = useState(props.text);

  function onClickOutSide(e) {
    // Check if user is clicking outside of <input>
    if (inputRef.current && !inputRef.current.contains(e.target)) {
      setInputVisible(false); // Disable text input
    }
  }

  useEffect(() => {
    // Handle outside clicks on mounted state
    if (inputVisible) {
      document.addEventListener("mousedown", onClickOutSide);
    }

    // This is a necessary step to "dismount" unnecessary events when we destroy the component
    return () => {
      document.removeEventListener("mousedown", onClickOutSide);
    };
  });

  let timer = 0;
  let delay = 200;
  let prevent = false;

  function doClickAction() {
    navigate(props.url)
  }
  function doDoubleClickAction() {
    setInputVisible(true)
  }

  function handleClick() {
    timer = setTimeout(function() {
      if (!prevent) {
        doClickAction();
      }
      prevent = false;
    }, delay);
  }

  function handleDoubleClick(){
    clearTimeout(timer);
    prevent = true;
    doDoubleClickAction();
  }

  function changeMenuItem(e) {
    setText(e.target.value);
    props.changeMenuItem(e);
  }

  return (
    <>
      {inputVisible ? (
        <input
          id={props.id}
          ref={inputRef} // Set the Ref
          value={text} // Now input value uses local state
          onChange={changeMenuItem}
        />
      ) : (
        // TODO: make nicer link, without window.location.href
        <div onClick={handleClick} 
          onDoubleClick = {handleDoubleClick}>
          {text}
        </div>
      )}
    </>
  );
};

function NavItems(props) {

  const [items, setItems] = useState([])

  useEffect(() => {
    setItems(props.items)
  }, props.items)

  const changeMenuItem = (e) => {

    let newItems = items.slice()
    // Change 1st level
    let firstLevelItem = newItems.find(el => el.id === e.target.id)
    if (firstLevelItem) {
      firstLevelItem.title = e.target.value
    } else {
      // Change 2nd levels
      newItems = newItems.map(item => {
        item.items = item.items && item.items.map(child => {
          if (child.url === e.target.id) child.title = e.target.value
          return child
        })
        return item
      })
    }
    setItems(newItems)
    parent.postMessage({ nav: newItems}, '*')
    
  }
  

  return (
    <>
      {items.map((item) => (
        <BorderBox
          key={item.title}
          border={0}
          borderRadius={0}
          borderTop={1}
          p={4}
          key={`NavItem__Box__${item.id}`}
        >
          <Flex flexDirection="column">
            {
              __EDIT__ ?
              <EditableInput
                  id={item.id}
                  url={item.url}
                  text={item.title}
                  changeMenuItem={changeMenuItem.bind(this)}
                />
              :
              <NavLink
                as={GatsbyLink}
                to={item.url}
                activeClassName="active"
                partiallyActive={true}
                color="inherit"
              >
                {item.title}
              </NavLink>
            }
            {item.items ? (
              <Flex flexDirection="column" mt={2} key={`NavItem__Flex__${item.id}`}>
                {item.items.map((child) => {
                    return __EDIT__ ?
                      <EditableInput
                        key={`NavItem__Edit__${child.url}`}
                        id={child.url}
                        url={child.url}
                        text={child.title}
                        changeMenuItem={changeMenuItem.bind(this)}
                      /> :
                      <NavLink
                        key={`NavItem__Link__${child.url}`}
                        as={GatsbyLink}
                        to={child.url}
                        activeClassName="active"
                        display="block"
                        py={1}
                        mt={2}
                        fontSize={1}
                      >
                        {child.title}
                      </NavLink>
                    }
                )}
              </Flex>
            ) : null}
          </Flex>
        </BorderBox>
      ))}
      {repositoryUrl ? (
        <BorderBox border={0} borderRadius={0} borderTop={1} p={4}>
          <Link href={repositoryUrl} color="inherit">
            <Flex justifyContent="space-between" alignItems="center">
              GitHub
              <LinkExternalIcon color="gray.7" />
            </Flex>
          </Link>
        </BorderBox>
      ) : null}
    </>
  )
}

export default NavItems
